import React from 'react'

const JobExperience = ({experiences}) => {
    return (
        <div className='lg:col-span-2'>
            <h1 className='text-2xl font-bold'>EXPERIENCE</h1>
            <ul>
                {
                    experiences.map((item) => {
                        return(
                            <li className='my-8' key={item.id}>
                                <h5 className='font-bold my-2'>{item.job_title}</h5>
                                <h6 className='font-semibold my-2'>{item.company} <span className='ml-2 text-sm font-normal text-gray-500 italic'> {item.period}</span></h6>
                                <ul className='list-disc ml-4'>
                                    {
                                        item.job_description.map((i) => {
                                            return (
                                                <li>{i}</li>
                                            )
                                        })
                                    }
                                </ul>
                            </li>
                        )
                    })
                }
            </ul>
        </div>
    )
}

export default JobExperience
